import React, { Component } from "react";
import { RoundThubline,ImgHolder, RoundThumbnailContainer } from "./general/style/style";
//import { toJS } from "mobx";
import { DhProperties } from "../DhProperties";


export default class RoundThumbnail extends Component {

  constructor(props){
    super(props);
    this.state={};
  }

  onViewAllClick = (viewAllUrl, viewAllTitle='') => {
    if(typeof(newsHuntAction)!='undefined' && typeof(callEvent)!="undefined" && ((!isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION)) || (isIos() && checkMinAndroidAppVersion(NH_BROWSER_SUPPORTED_VERSION_IOS))) && viewAllUrl){
      var url= DhProperties.properties.pwaBaseUrl+"/webItem?webModel="+encodeURIComponent(JSON.stringify({title:viewAllTitle,url:DhProperties.properties.baseUrl +`/${viewAllUrl}`}));
      callEvent(url);
    } else if(viewAllUrl){ 
     window.location.href = DhProperties.properties.baseUrl +`/${viewAllUrl}`;
    }
  }

  render() {
    let className = this.props.className || "" + "NoBorder theme-brand";
    className = className.trim();

    return (
      <RoundThumbnailContainer className={className} onClick={()=>this.onViewAllClick(this.props.data.viewAllUrl, this.props.data.viewAllTitle)} >
        <ImgHolder height="50px !important" width="50px !important">
          <img src={this.props.data.logo} alt={this.props.data.name} />
        </ImgHolder>
        <p className="name">{this.props.data.name}</p>
      </RoundThumbnailContainer>
    );
  }
}





